import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { RespuestaLogin } from 'src/app/commons/interfaces/respuesta-login-interface';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(public http: HttpClient,
    public router: Router,
    public authService: AuthenticationService) { }

  public login(username: string, password: string, tipo: string = '') {
    let url = `${environment.oauth_url}oauth/token`;
    let bodyH: HttpParams;
    if (tipo === 'd') {
      //url = url.replace('/v2/', '/');
      this.authService.guardarID(username);
      bodyH = new HttpParams()
        .set('username', username)
        .set('password', btoa(password))
        .set('grant_type', 'password')
        .set('id_sistema', environment.id_sistema)
        .set('conection_key', environment.conection_key)
        .set('esDocente', 'S')
        .set('esBase64', '1');

    } else {
      bodyH = new HttpParams()
        .set('username', username)
        .set('password', btoa(password))
        .set('grant_type', 'password')
        .set('id_sistema', environment.id_sistema)
        .set('conection_key', environment.conection_key)
        .set('esBase64', '1');
    }
    const options = {
      headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded; charset=utf-8')
        .set('Authorization', 'Basic Y2xpZW50UGxhdGZvcm06MTIzNDU2'),
      body: bodyH
    };
    return this.http.post<RespuestaLogin>(url, bodyH.toString(), options);
  }
}