import * as fromActions from '../actions/resolucion.actions';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';
import { ResolucionModel } from 'src/app/commons/models/gestion-academica/resolucion.model';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { Action, createReducer, on } from '@ngrx/store';

export interface ResolucionState {
    loading: boolean;
    loaded: boolean;
    paginacionResolucion: IPaginacion<ResolucionModel>;
    resolucion: ResolucionModel;
    error: any;
    searchPagination: SearchPagination<String>;
    mensaje: string;
}

const initState: ResolucionState = {
    loading: false,
    loaded: false,
    paginacionResolucion: null,
    error: null,
    searchPagination: null,
    mensaje: null,
    resolucion: null,


};

const _resolucionReducer = createReducer(initState,

    on(fromActions.paginacionResolucion, (state, { searchPagination }): ResolucionState => ({
        ...state,
        loading: true,
        loaded: false,
        error: null,
        paginacionResolucion: null,
        resolucion: null,
        searchPagination: searchPagination
    })),
    on(fromActions.paginacionResolucionSuccess, (state, { paginadoResolucion }): ResolucionState => ({
        ...state,
        paginacionResolucion: paginadoResolucion,
        loading: false,
        loaded: true,
    })),
    on(fromActions.paginacionResolucionFail, (state, { mensaje }): ResolucionState => ({
        ...state,
        loading: false,
        error: mensaje
    })),
);

export function resolucionReducer(state: ResolucionState | undefined, action: Action) {
    return _resolucionReducer(state, action);
}