import { HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

import { UtilAlert } from '../../util/util.alert';
import { AuthenticationService } from '../authentication.service';


@Injectable()
export class JwtInterceptor implements HttpInterceptor {

  constructor(
    private auth: AuthenticationService,
    private _alert: UtilAlert,
    private toastr: ToastrService,
  ) { }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    return next.handle(request).pipe(tap((event: HttpEvent<any>) => { },
      (err: any) => {
        if (err instanceof HttpErrorResponse) {
          if (err.status === 401) {
            console.log(err);
            this.toastr.error(err?.error?.message, '', { disableTimeOut: true });
            this._alert.errorSwal('Su sesión ha finalizado');
            this.auth.logout();
          }
          if (err.status === 403) {
            console.log(err);

            this.toastr.error(err?.error?.message, '', { disableTimeOut: true });
            this._alert.errorSwal('No tienes Permisos para ejecutar esta operación');
            this.auth.logout();
          }
          if (err.status === 0) {
            this.toastr.warning('El servidor se ha desconectado, le pedimos esperar hasta que el servicio esté disponible.', '', { disableTimeOut: true });
            // this.auth.logout();
          }
        }
      }));
  }
}
