import * as fromActions from '../actions/resolucion-docente.actions';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';
import { ResolucionDocenteModel } from 'src/app/commons/models/gestion-academica/resolucion-docente.model';
import { createReducer, on, Action } from '@ngrx/store';
import { SearchPagination } from '../../commons/interfaces/search.pagination';

export interface ResolucionDocenteState {
  loadingGuardado: boolean;
  loadedGuardado: boolean;
  resolucionDocente: ResolucionDocenteModel[];
  error: any;
}

const initState: ResolucionDocenteState = {
  loadingGuardado: false,
  loadedGuardado: false,
  resolucionDocente: null,
  error: null,
};

const _resolucionDocenteReducer = createReducer(initState,
  on(fromActions.guardarResolucionDocente, (state, { resolucionDocente }): ResolucionDocenteState => ({
    ...state,
    resolucionDocente: resolucionDocente,
    loadingGuardado: true,
    loadedGuardado: false,
    error: null,
  })),
  on(fromActions.guardarResolucionDocenteSuccess, (state, { resolucionDocente }): ResolucionDocenteState => ({
    ...state,
    resolucionDocente: resolucionDocente,
    loadingGuardado: false,
    loadedGuardado: true,
  })),
  on(fromActions.guardarResolucionDocenteFail, (state, { error }): ResolucionDocenteState => ({
    ...state,
    loadingGuardado: false,
    error: error,
  })),
  on(fromActions.destroyResolucionDocente, (): ResolucionDocenteState => initState)
);
export function resolucionDocenteReducer(state: ResolucionDocenteState | undefined, action: Action) {
  return _resolucionDocenteReducer(state, action);
}