import { createAction, props } from '@ngrx/store';
import { PreguntaRespuestaModel } from 'src/app/commons/models/aerc-models/pregunta-respuesta.model';

export const listarPreguntaSolicitud = createAction(
    '[FORMULARIO MODEL] Get formulario solicitud',
    props<{ tipoSolicitud: number, idSolicitud: number }>()
);
export const listarPreguntaSolicitudSuccess = createAction(
    '[FORMULARIO MODEL] Get formulario solicitud success',
    props<{ listadoSolicitud: Array<PreguntaRespuestaModel> }>()
);
export const listarPreguntaSolicitudFail = createAction(
    '[FORMULARIO MODEL] Get formulario solicitud fail',
    props<{ mensaje: string }>()
);

export const getEncuesta = createAction(
    '[FORMULARIO MODEL] Get encuesta',
    props<{ idSolicitud: number }>()
);
export const getEncuestaSuccess = createAction(
    '[FORMULARIO MODEL] Get encuesta success',
    props<{ listadoEncuesta: Array<PreguntaRespuestaModel> }>()
);
export const getEncuestaFail = createAction(
    '[FORMULARIO MODEL] Get encuesta fail',
    props<{ mensaje: string }>()
);

export const unsetFormulario = createAction(
    '[FORMULARIO MODEL] Unset formulario state');

