import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, exhaustMap, map } from 'rxjs/operators';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as solicitudActions from '../../actions/aerc-actions/solicitud.actions';
import { Router } from '@angular/router';
import { SolicitudService } from 'src/app/commons/services/aerc-services/solicitud.service';

@Injectable()
export class SolicitudEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toastr: ToastrService,
    private actions$: Actions,
    private alert: UtilAlert,
    private solicitudService: SolicitudService,
    private router: Router
  ) { }

  listarSolicitudes$ = createEffect(() =>
    this.actions$.pipe(
      ofType(solicitudActions.listarSolicitudes),
      exhaustMap(action =>
        this.solicitudService.listarSolicitudes(action.searchPagination, action.estadoSolicitud, action.tipoSolicitud, action.periodo).pipe(
          map(salida => {
            if (salida != null) {
              return solicitudActions.listarSolicitudesSuccess({ listadoSolicitudesPaginacion: salida });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las solicitudes creadas`;
            return solicitudActions.listarSolicitudesFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(solicitudActions.listarSolicitudesFail({ mensaje: error?.error?.message }));
          })
        )
      )
    ))

  listarSolicitudesInnovacion$ = createEffect(() =>
    this.actions$.pipe(
      ofType(solicitudActions.listarSolicitudesInnovacion),
      exhaustMap(action =>
        this.solicitudService.listarSolicitudesInnovacion(action.searchPagination, action.estadoSolicitud, action.tipoSolicitud, action.isAprobador).pipe(
          map(salida => {
            if (salida != null) {
              return solicitudActions.listarSolicitudesInnovacionSuccess({ listadoSolicitudesPaginacion: salida });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las solicitudes creadas`;
            return solicitudActions.listarSolicitudesInnovacionFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(solicitudActions.listarSolicitudesInnovacionFail({ mensaje: error?.error?.message }));
          })
        )
      )
    ))

  getSolicitudesByTipoD$ = createEffect(() =>
    this.actions$.pipe(
      ofType(solicitudActions.getSolicitudesByTipo),
      exhaustMap(action =>
        this.solicitudService.getSolicitudesByTipo(action.tipoSolicitud).pipe(
          map(salida => {
            console.log("BYTIPO");
            if (salida != null) {
              return solicitudActions.getSolicitudesByTipoSuccess({ listadoSolicitudes: salida });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las solicitudes creadas`;
            return solicitudActions.getSolicitudesByTipoFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(solicitudActions.getSolicitudesByTipoFail({ mensaje: error?.error?.message }));
          })
        )
      )
    ))

}
