import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, concatMap, exhaustMap, map } from 'rxjs/operators';
import { IndicadorPublicacionesService } from 'src/app/commons/services/indicador-publicaciones.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as indicadorPublicacionesActions from '../actions/indicador-publicaciones.actions';

@Injectable()
export class IndicadorPublicacionesEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private indicadorPublicacionesService: IndicadorPublicacionesService,
        private alert: UtilAlert,
        private router: Router
    ) { }
    paginacionIndicacorPublicaciones$ = createEffect(() =>
        this.actions$.pipe(
            ofType(indicadorPublicacionesActions.paginacionIndicadorPublicaciones),
            concatMap((data) =>
                this.indicadorPublicacionesService
                    .pagination(data.searchPagination)
                    .pipe(
                        map((resul) => {
                            if (resul != null) {
                                return indicadorPublicacionesActions.paginacionIndicadorPublicacionesSuccess({
                                    paginadoPeriodo: resul,
                                });
                            }
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                            this.toastr.error(msgError);
                            return indicadorPublicacionesActions.paginacionIndicadorPublicacionesFail({
                                error: msgError,
                            });
                        }),
                        catchError((error) => {
                            if (error.status == 0) {
                                const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                                this.toastr.error(msgError);
                                return of(
                                    indicadorPublicacionesActions.paginacionIndicadorPublicacionesFail({
                                        error: msgError,
                                    })
                                );
                            }
                            this.toastr.error(error?.error?.message);
                            return of(
                                indicadorPublicacionesActions.paginacionIndicadorPublicacionesFail({
                                    error: error?.error?.message,
                                })
                            );
                        })
                    )
            )
        )
    );

    guardarIndicadorPublicaciones$ = createEffect(() =>
        this.actions$.pipe(
            ofType(indicadorPublicacionesActions.guardarIndicadorPublicaciones),
            exhaustMap(action =>
                this.indicadorPublicacionesService.save(action.indicadorPublicaciones).pipe(
                    map(resul => {
                        if (resul != null) {
                            return indicadorPublicacionesActions.guardarIndicadorPublicacionesSuccess({ indicadorPublicaciones: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar guardar el indicador de Publicaciones - Adjudicaciones`;
                        this.toastr.error(msgError);
                        return indicadorPublicacionesActions.guardarIndicadorPublicacionesFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar guardar el indicador de Publicaciones - Adjudicaciones`;
                            this.toastr.error(msgError);
                            return of(indicadorPublicacionesActions.guardarIndicadorPublicacionesFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(indicadorPublicacionesActions.guardarIndicadorPublicacionesFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}