import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { PeriodoModel } from '../models/gestion-academica/periodo.model';
import { SearchPagination } from '../interfaces/search.pagination';
import { IPaginacion } from '../interfaces/paginacion.interface';
@Injectable({
    providedIn: 'root'
})
export class PeriodoService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllPeriodos(): Observable<Array<PeriodoModel>> {
        return this.http.get<Array<PeriodoModel>>(`${environment.backend.gestion_academica}api/periodo/v1/get-all`);
    }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<PeriodoModel>> {
        const url = `${environment.backend.gestion_academica}api/periodo/v1/pagination`;
        return this.http.post<IPaginacion<PeriodoModel>>(url, searchPagination);
    }

    public getAllPeriodosCompleto(): Observable<Array<PeriodoModel>> {
        return this.http.get<Array<PeriodoModel>>(`${environment.backend.gestion_academica}api/periodo/v1/get-all-periodos`);
    }

    public save(periodo: PeriodoModel): Observable<PeriodoModel> {
        return this.http.post<PeriodoModel>(`${environment.backend.gestion_academica}api/periodo`, periodo);
    }

}