import { createAction, props } from '@ngrx/store';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { ConfiguracionCompromisoModel } from 'src/app/commons/models/gestion-academica/configuracion-compromiso.model';

// PAGINACION
export const guardarConfiguracionCompromisos = createAction(
    '[CONFIGURACION COMPROMISOS MODEL] Guardar configuracionCompromiso',
    props<{ configuracionCompromiso: ConfiguracionCompromisoModel }>()
);
export const guardarConfiguracionCompromisosSuccess = createAction(
    '[CONFIGURACION COMPROMISOS MODEL] Guardar configuracionCompromiso success',
    props<{ configuracionCompromiso: ConfiguracionCompromisoModel }>()
);
export const guardarConfiguracionCompromisosFail = createAction(
    '[CONFIGURACION COMPROMISOS MODEL] Guardar configuracionCompromiso fail',
    props<{ error: any }>()
);
export const resetConfiguracionCompromisos = createAction(
    '[CONFIGURACION COMPROMISOS MODEL] RESET PERIODOS'
);

export const paginacionConfiguracionCompromisos = createAction(
    "[CONFIGURACION COMPROMISOS PAGINACION] Listar all PERIODO",
    props<{ searchPagination: SearchPagination<string> }>()
);
export const paginacionConfiguracionCompromisosSuccess = createAction(
    "[CONFIGURACION COMPROMISOS PAGINACION] Listar all PERIODO success",
    props<{ paginadoPeriodo: IPaginacion<ConfiguracionCompromisoModel> }>()
);
export const paginacionConfiguracionCompromisosFail = createAction(
    "[CONFIGURACION COMPROMISOS PAGINACION] Listar all PERIODO fail",
    props<{ error: any }>()
);
