import { Action, createReducer, on } from '@ngrx/store';
import { Paso } from 'src/app/commons/enums/paso-enum';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { DatoPagoModel } from 'src/app/commons/models/dato-pago.model';
import { PeriodoModel } from 'src/app/commons/models/gestion-academica/periodo.model';
import { ResultadoPagoModel } from 'src/app/commons/models/resultado-pago.model';

import * as fromActions from '../actions/periodo.actions';

export interface PeriodoState {
    loading: boolean;
    loaded: boolean;
    saving: boolean;
    saved: boolean;
    success: boolean;
    listaPeriodos: PeriodoModel[];
    error: any;
    paginacionPeriodo: IPaginacion<PeriodoModel>;
    searchPagination: SearchPagination<String>;
    periodo: PeriodoModel;
}

const initState: PeriodoState = {
    loading: false,
    loaded: false,
    saving: false,
    saved: false,
    listaPeriodos: null,
    success: null,
    error: null,
    paginacionPeriodo: null,
    searchPagination: null,
    periodo: null,

};

const _periodoReducer = createReducer(initState,

    on(fromActions.getPeriodos, (state): PeriodoState => ({
        ...state,
        loading: true,
        error: null,
        success: null,
        saved: false,
        listaPeriodos: null,
    })),
    on(fromActions.getPeriodosSuccess, (state, { listaPeriodos }): PeriodoState => ({
        ...state,
        loading: false,
        listaPeriodos,
    })),
    on(fromActions.getPeriodosFail, (state, { mensaje }): PeriodoState => ({
        ...state,
        loading: false,
        error: mensaje,
    })),
    on(
        fromActions.paginationPeriodo,
        (state, { searchPagination }): PeriodoState => ({
            ...state,
            loading: true,
            loaded: false,
            error: null,
            paginacionPeriodo: null,
            searchPagination: searchPagination,
        })
    ),
    on(
        fromActions.paginationPeriodoSuccess,
        (state, { paginadoPeriodo }): PeriodoState => ({
            ...state,
            paginacionPeriodo: paginadoPeriodo,
            loading: false,
            loaded: true,

        })
    ),
    on(
        fromActions.paginationPeriodoFail,
        (state, { error }): PeriodoState => ({
            ...state,
            loading: false,
            error: error,
        })
    ),
    on(fromActions.guardarPeriodo, (state, { periodo }): PeriodoState => ({
        ...state,
        periodo: periodo,
        loading: true,
        loaded: false,
        error: null,
    })),
    on(fromActions.guardarPeriodoSuccess, (state, { periodo }): PeriodoState => ({
        ...state,
        periodo: periodo,
        loading: false,
        loaded: true,
    })),
    on(fromActions.guardarPeriodoFail, (state, { error }): PeriodoState => ({
        ...state,
        loading: false,
        error: error,
    })),
    on(fromActions.resetPeriodo, (): PeriodoState => initState),
);

export function periodoReducer(state: PeriodoState | undefined, action: Action) {
    return _periodoReducer(state, action);
}

