import * as fromActions from '../actions/director.actions';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { Action, createReducer, on } from '@ngrx/store';
import { DirectorModel } from 'src/app/commons/models/gestion-academica/director.model';

export interface DirectorState {
    loading: boolean;
    loadingGuardado: boolean;
    loaded: boolean;
    loadedGuardado: boolean;
    paginacionDirector: IPaginacion<DirectorModel>;
    director: DirectorModel;
    error: any;
    searchPagination: SearchPagination<String>;
    mensaje: string;
}

const initState: DirectorState = {
    loading: false,
    loadingGuardado: false,
    loaded: false,
    loadedGuardado: false,
    paginacionDirector: null,
    error: null,
    searchPagination: null,
    mensaje: null,
    director: null,



};

const _directorReducer = createReducer(initState,

    on(fromActions.paginacionDirector, (state, { searchPagination }): DirectorState => ({
        ...state,
        loading: true,
        loaded: false,
        error: null,
        paginacionDirector: null,
        director: null,
        searchPagination: searchPagination
    })),
    on(fromActions.paginacionDirectorSuccess, (state, { paginadoDirector }): DirectorState => ({
        ...state,
        paginacionDirector: paginadoDirector,
        loading: false,
        loaded: true,
    })),
    on(fromActions.paginacionDirectorFail, (state, { mensaje }): DirectorState => ({
        ...state,
        loading: false,
        error: mensaje,
    })),
    on(fromActions.editarDirector, (state, { Director }): DirectorState => ({
        ...state,
        director: Director,
        loadingGuardado: true,
        loadedGuardado: false,
        error: null,
    })),
    on(fromActions.editarDirectorSuccess, (state, { Director }): DirectorState => ({
        ...state,
        director: Director,
        loadingGuardado: false,
        loadedGuardado: true,
    })),
    on(fromActions.editarDirectorFail, (state, { error }): DirectorState => ({
        ...state,
        loadingGuardado: false,
        error: error,
    })),
    on(fromActions.resetDirector, (): DirectorState => initState),
);

export function directorReducer(state: DirectorState | undefined, action: Action) {
    return _directorReducer(state, action);
}