import { createAction, props } from '@ngrx/store';
import { EstadoSolicitudModel } from 'src/app/commons/models/aerc-models/estado-solicitud-model';

export const getEstadoSolicitud = createAction(
    '[ESTADO SOLICITUD MODEL] Get listado estado solicitud',
    props<{ tipoSolicitud: string }>()
);
export const getEstadoSolicitudSuccess = createAction(
    '[ESTADO SOLICITUD MODEL] Get listado estado solicitud success',
    props<{ estados: Array<EstadoSolicitudModel> }>()
);
export const getEstadoSolicitudFail = createAction(
    '[ESTADO SOLICITUD MODEL] Get listado estado solicitud fail',
    props<{ mensaje: string }>()
);

export const unsetEstadoSolicitud = createAction(
    '[ESTADO SOLICITUD MODEL] Unset usuario state');

