import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';
import { AlumnoSolicitudModel } from '../../models/aerc-models/alumno-solicitud.model';

@Injectable({
  providedIn: 'root'
})
export class SolicitudService {


  private urlBase = `${environment.backend.aerc}api/solicitud-admin`;
  constructor(
    private http: HttpClient,
  ) { }

  public findById(idSolicitud: number): Observable<SolicitudModel> {
    return this.http.get<SolicitudModel>(`${environment.backend.aerc}api/solicitud/v1/find?idSolicitud=${idSolicitud}`);
  }

  public findAllSolicitudesByIdBanner(idBanner: string): Observable<Array<SolicitudModel>> {
    return this.http.get<Array<SolicitudModel>>(`${environment.backend.aerc}api/solicitud/v1/find-all/${idBanner}`);
  }

  public listarSolicitudes(searchPagination: SearchPagination<string>,
    estadoSolicitud: number, tipoSolicitud: number, periodoAcademico: number): Observable<IPaginacion<SolicitudModel>> {
    return this.http.post<IPaginacion<SolicitudModel>>(`${this.urlBase}/v1/get-all-solicitudes/paginated/${estadoSolicitud}/${tipoSolicitud}/${periodoAcademico}`, searchPagination);
  }

  public listarSolicitudesInnovacion(searchPagination: SearchPagination<string>,
    estadoSolicitud: number, tipoSolicitud: number, isAprobador: boolean): Observable<IPaginacion<SolicitudModel>> {
    return this.http.post<IPaginacion<SolicitudModel>>(`${environment.backend.aerc}api/solicitud/v1/get-all-solicitudes-innovacion/${estadoSolicitud}/${tipoSolicitud}?isAprobador=${isAprobador}`, searchPagination);
  }

  public getSolicitudesByTipo(tipoServicio: number): Observable<Array<SolicitudModel>> {
    return this.http.get<Array<SolicitudModel>>(`${this.urlBase}/v1/get-all-solicitudes/${tipoServicio}`);
  }

  public getAllTipoSolicitud(): Observable<Array<TipoSolicitudModel>> {
    return this.http.get<Array<TipoSolicitudModel>>(`${this.urlBase}/v1/get-all-tipo-solicitud/`);
  }

  public getAllTipoSolicitudAcademica(): Observable<Array<TipoSolicitudModel>> {
    return this.http.get<Array<TipoSolicitudModel>>(`${this.urlBase}/v1/get-all-tipo-solicitud/academicas`);
  }

  public habilitarEdicion(solicitud: SolicitudModel): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${this.urlBase}/v1/habilitar-solicitud/${solicitud.id}`, solicitud);
  }

  public aprobarSolicitud(solicitud: SolicitudModel): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${this.urlBase}/v1/aprobar-solicitud/${solicitud.id}`, solicitud);
  }

  public rechazarSolicitud(solicitud: SolicitudModel): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${this.urlBase}/v1/rechazar-solicitud/${solicitud.id}`, solicitud);
  }

  public rechazarSolicitudSuspensionFueraReglamento(solicitud: SolicitudModel): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${this.urlBase}/v1/rechazar-solicitud/suspension/${solicitud.id}`, solicitud);
  }

  public rechazarSolicitudCurricular(solicitud: SolicitudModel): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${this.urlBase}/v1/rechazar-solicitud-innovacion/${solicitud.id}`, solicitud);
  }

  public crearSolicitudInnovacion(alumnoSolicitud: AlumnoSolicitudModel, tipoSolicitud: number): Observable<SolicitudModel> {
    return this.http.post<SolicitudModel>(`${environment.backend.aerc}api/solicitud/v1/save/solicitud-academica/${tipoSolicitud}`, alumnoSolicitud);
  }

  public marcarFueraPlazo(solicitud: SolicitudModel): Observable<boolean> {
    return this.http.post<boolean>(`${environment.backend.aerc}api/solicitud/v1/marcar-fuera-plazo`, solicitud);
  }

}
