import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, exhaustMap, map } from 'rxjs/operators';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as aprobadorActions from '../../actions/aerc-actions/aprobador.actions';
import { Router } from '@angular/router';
import { AprobadorAercService } from '../../../commons/services/aerc-services/aprobador-aerc.service';

@Injectable()
export class AprobadorEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toastr: ToastrService,
    private actions$: Actions,
    private alert: UtilAlert,
    private aprobadorService: AprobadorAercService,
  ) { }

  listarAprobadores$ = createEffect(() =>
    this.actions$.pipe(
      ofType(aprobadorActions.listarAprobadores),
      exhaustMap(action =>
        this.aprobadorService.listarAprobadores(action.searchPagination, action.escuela, action.facultad, action.tipo).pipe(
          map(salida => {
            if (salida != null) {
              return aprobadorActions.listarAprobadoresSuccess({ listadoAprobadoresPaginated: salida });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener los aprobadores`;
            return aprobadorActions.listarAprobadoresFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(aprobadorActions.listarAprobadoresFail({ mensaje: error?.error?.message }));
          })
        )
      )
    ))

}
