import { createAction, props } from '@ngrx/store';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';

//** SOLICITUDES DE ESTUDIANTES */
export const listarSolicitudes = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes',
    props<{ searchPagination: SearchPagination<string>, estadoSolicitud: number, tipoSolicitud: number, periodo: number }>()
);
export const listarSolicitudesSuccess = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes success',
    props<{ listadoSolicitudesPaginacion: IPaginacion<SolicitudModel> }>()
);
export const listarSolicitudesFail = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes fail',
    props<{ mensaje: string }>()
);

//** SOLICITUDES DE INNOVACIÖN DE ESTUDIOS */
export const listarSolicitudesInnovacion = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes Innovacion',
    props<{ searchPagination: SearchPagination<string>, estadoSolicitud: number, tipoSolicitud: number, isAprobador: boolean }>()
);
export const listarSolicitudesInnovacionSuccess = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes Innovacion success',
    props<{ listadoSolicitudesPaginacion: IPaginacion<SolicitudModel> }>()
);
export const listarSolicitudesInnovacionFail = createAction(
    '[SOLICITUD MODEL] Listar Solicitudes Innovacion fail',
    props<{ mensaje: string }>()
);

export const getSolicitudesByTipo = createAction(
    '[SOLICITUD MODEL] Get solicitudes by tipo',
    props<{ tipoSolicitud: number }>()
);
export const getSolicitudesByTipoSuccess = createAction(
    '[SOLICITUD MODEL] Get solicitudes success',
    props<{ listadoSolicitudes: Array<SolicitudModel> }>()
);
export const getSolicitudesByTipoFail = createAction(
    '[SOLICITUD MODEL] Get solicitudes fail',
    props<{ mensaje: string }>()
);

export const setVistaSolicitud = createAction(
    '[SOLICITUD MODEL] Set vista solicitud',
    props<{ solicitud: SolicitudModel }>()
);

export const unsetSolicitud = createAction(
    '[SOLICITUD MODEL] Unset usuario state');

