import { Action, createReducer, on } from '@ngrx/store';


import * as fromActions from '../../actions/aerc-actions/aprobador.actions';

import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { AprobadorAercModel } from '../../../commons/models/aerc-models/aprobador-aerc.model';
import { listarAprobadores } from '../../actions/aerc-actions/aprobador.actions';

export interface AprobadorState {
  loading: boolean;
  error: any;
  aprobador: AprobadorAercModel;
  listadoAprobadores: Array<AprobadorAercModel>;
  listadoAprobadoresPaginated: IPaginacion<AprobadorAercModel>;
  searchPagination: SearchPagination<string>;
  escuela: string;
  facultad: string;
  tipo: number;
}

const initState: AprobadorState = {
  loading: false,
  error: null,
  aprobador: null,
  listadoAprobadores: null,
  listadoAprobadoresPaginated: null,
  searchPagination: null,
  escuela: null,
  facultad: null,
  tipo: null,
};

const _aprobadorReducer = createReducer(initState,

  on(fromActions.listarAprobadores, (state, { searchPagination, escuela, facultad, tipo }): AprobadorState => ({
    ...state,
    loading: true,
    error: null,
    escuela: escuela,
    facultad: facultad,
    tipo: tipo,
    searchPagination: searchPagination,
  })),
  on(fromActions.listarAprobadoresSuccess, (state, { listadoAprobadoresPaginated }): AprobadorState => ({
    ...state,
    loading: false,
    listadoAprobadoresPaginated: listadoAprobadoresPaginated,
  })),
  on(fromActions.listarAprobadoresFail, (state, { mensaje }): AprobadorState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),
  on(fromActions.unsetSolicitud, (): AprobadorState => initState),
);

export function aprobadorReducer(state: AprobadorState | undefined, action: Action) {
  return _aprobadorReducer(state, action);
}

