import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, exhaustMap, map } from 'rxjs/operators';
import { EstadoSolicitudService } from 'src/app/commons/services/aerc-services/estado-solicitud.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as estadoSolicitudActions from '../../actions/aerc-actions/estado-solicitud.actions';

@Injectable()
export class EstadoSolicitudEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toastr: ToastrService,
    private actions$: Actions,
    private estadoSolicitudService: EstadoSolicitudService,
    private alert: UtilAlert,
  ) { }

  getEstadoSolicitud$ = createEffect(() =>
    this.actions$.pipe(
      ofType(estadoSolicitudActions.getEstadoSolicitud),
      exhaustMap(action =>
        this.estadoSolicitudService.getAllEstadoSolicitud(action.tipoSolicitud).pipe(
          map(result => {
            if (result != null) {
              return estadoSolicitudActions.getEstadoSolicitudSuccess({ estados: result });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener los usuarios`;
            this.toastr.error(msgError);
            return estadoSolicitudActions.getEstadoSolicitudFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(estadoSolicitudActions.getEstadoSolicitudFail({ mensaje: error?.error?.message }));
          })
        )
      )
    )
  );

}
