import { createAction, props } from '@ngrx/store';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { AprobadorAercModel } from 'src/app/commons/models/aerc-models/aprobador-aerc.model';
import { ResolucionAercModel } from '../../../commons/models/aerc-models/resolucion-aerc.model';

export const listarAprobadores = createAction(
    '[APROBADOR MODEL] Listar aprobadores',
    props<{ searchPagination: SearchPagination<string>, escuela: string, facultad: string, tipo: number }>()
);
export const listarAprobadoresSuccess = createAction(
    '[APROBADOR MODEL] Listar aprobadores success',
    props<{ listadoAprobadoresPaginated: IPaginacion<AprobadorAercModel> }>()
);
export const listarAprobadoresFail = createAction(
    '[APROBADOR MODEL] Listar aprobadores fail',
    props<{ mensaje: string }>()
);

export const unsetSolicitud = createAction(
    '[APROBADOR MODEL] Unset aprobador state');

