import { Action, createReducer, on } from '@ngrx/store';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { ConfiguracionCompromisoModel } from 'src/app/commons/models/gestion-academica/configuracion-compromiso.model';
import * as fromActions from 'src/app/store/actions/configuracion-compromiso.actions'

export interface ConfiguracionCompromisoState {
    loading: boolean;
    loaded: boolean;
    saving: boolean;
    saved: boolean;
    success: boolean;
    listaConfiguracionCompromiso: ConfiguracionCompromisoModel[];
    error: any;
    paginacionConfiguracionCompromiso: IPaginacion<ConfiguracionCompromisoModel>;
    searchPagination: SearchPagination<String>;
    configuracionCompromiso: ConfiguracionCompromisoModel;
}

const initState: ConfiguracionCompromisoState = {
    loading: false,
    loaded: false,
    saving: false,
    saved: false,
    listaConfiguracionCompromiso: null,
    success: null,
    error: null,
    paginacionConfiguracionCompromiso: null,
    searchPagination: null,
    configuracionCompromiso: null,

};

const _configuracionCompromisoReducer = createReducer(initState,
    on(
        fromActions.paginacionConfiguracionCompromisos,
        (state, { searchPagination }): ConfiguracionCompromisoState => ({
            ...state,
            loading: true,
            loaded: false,
            error: null,
            paginacionConfiguracionCompromiso: null,
            searchPagination: searchPagination,
        })
    ),
    on(
        fromActions.paginacionConfiguracionCompromisosSuccess,
        (state, { paginadoPeriodo }): ConfiguracionCompromisoState => ({
            ...state,
            paginacionConfiguracionCompromiso: paginadoPeriodo,
            loading: false,
            loaded: true,

        })
    ),
    on(
        fromActions.paginacionConfiguracionCompromisosFail,
        (state, { error }): ConfiguracionCompromisoState => ({
            ...state,
            loading: false,
            error: error,
        })
    ),
    on(fromActions.guardarConfiguracionCompromisos, (state, { configuracionCompromiso }): ConfiguracionCompromisoState => ({
        ...state,
        configuracionCompromiso: configuracionCompromiso,
        loading: true,
        loaded: false,
        error: null,
    })),
    on(fromActions.guardarConfiguracionCompromisosSuccess, (state, { configuracionCompromiso }): ConfiguracionCompromisoState => ({
        ...state,
        configuracionCompromiso: configuracionCompromiso,
        loading: false,
        loaded: true,
    })),
    on(fromActions.guardarConfiguracionCompromisosFail, (state, { error }): ConfiguracionCompromisoState => ({
        ...state,
        loading: false,
        error: error,
    })),

    on(fromActions.resetConfiguracionCompromisos, (): ConfiguracionCompromisoState => initState),
);

export function configuracionCompromisoReducer(state: ConfiguracionCompromisoState | undefined, action: Action) {
    return _configuracionCompromisoReducer(state, action);
}

