import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { PeriodoModel } from '../models/gestion-academica/periodo.model';
import { SearchPagination } from '../interfaces/search.pagination';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { ConfiguracionCompromisoModel } from '../models/gestion-academica/configuracion-compromiso.model';
@Injectable({
    providedIn: 'root'
})
export class ConfiguracionCompromisoService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllConfiguracionCompromiso(): Observable<Array<ConfiguracionCompromisoModel>> {
        return this.http.get<Array<ConfiguracionCompromisoModel>>(`${environment.backend.gestion_academica}api/configuracion-compromiso/v1/get-all`);
    }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<ConfiguracionCompromisoModel>> {
        const url = `${environment.backend.gestion_academica}api/configuracion-compromiso/v1/pagination`;
        return this.http.post<IPaginacion<ConfiguracionCompromisoModel>>(url, searchPagination);
    }

    public save(configuracionCompromiso: ConfiguracionCompromisoModel): Observable<ConfiguracionCompromisoModel> {
        return this.http.post<ConfiguracionCompromisoModel>(`${environment.backend.gestion_academica}api/configuracion-compromiso`, configuracionCompromiso);
    }

    public activarAllConfiguracion(): Observable<boolean> {
        return this.http.get<boolean>(`${environment.backend.gestion_academica}api/configuracion-compromiso/v1/activar-all`);
    }

    public desactivarAllConfiguracion(): Observable<boolean> {
        return this.http.get<boolean>(`${environment.backend.gestion_academica}api/configuracion-compromiso/v1/desactivar-all`);
    }



}