import { DirectorModel } from 'src/app/commons/models/gestion-academica/director.model';


// PAGINACION
export const paginacionDirector = createAction(
    '[DIRECTOR MODEL] Paginación Director',
    props<{ searchPagination: SearchPagination<string> }>()
);
