import { Component, OnDestroy, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { AppService } from 'src/app/app.service';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';

@Component({
  selector: 'app-home-index',
  templateUrl: './home-index.component.html',
  styleUrls: [
    '../../../../vendor/styles/pages/authentication.scss'
  ]
})
export class HomeIndexComponent implements OnInit, OnDestroy {

  public height = 0;
  private token = '';
  private idSistema = '';
  public url = null;
  public urlLista = "http://localhost:4200/#/blank/curriculum/listado"
  public mostrar = false;
  public esDocente: boolean;

  constructor(
    private appService: AppService,
    private authService: AuthenticationService,
    public sanitizer: DomSanitizer,
  ) {
    this.height = window.innerHeight;
    this.token = this.authService.obtenerToken();
    this.url = "http://localhost:4200/#/login/" + this.token;

  }
  ngOnDestroy() {
  }
  ngOnInit() {
    if (isNaN(parseInt(this.authService.obtenerUserName()))) {
      this.esDocente = false
    } else {
      this.esDocente = true;
    }
    console.log(this.esDocente)
  }


}
