import * as fromActions from '../actions/decano.actions';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { Action, createReducer, on } from '@ngrx/store';
import { DecanoModel } from 'src/app/commons/models/gestion-academica/decano.model';

export interface DecanoState {
    loading: boolean;
    loadingGuardado: boolean;
    loaded: boolean;
    loadedGuardado: boolean;
    paginacionDecano: IPaginacion<DecanoModel>;
    decano: DecanoModel;
    error: any;
    searchPagination: SearchPagination<String>;
    mensaje: string;
}

const initState: DecanoState = {
    loading: false,
    loadingGuardado: false,
    loaded: false,
    loadedGuardado: false,
    paginacionDecano: null,
    error: null,
    searchPagination: null,
    mensaje: null,
    decano: null,



};

const _decanoReducer = createReducer(initState,

    on(fromActions.paginacionDecano, (state, { searchPagination }): DecanoState => ({
        ...state,
        loading: true,
        loaded: false,
        error: null,
        paginacionDecano: null,
        decano: null,
        searchPagination: searchPagination
    })),
    on(fromActions.paginacionDecanoSuccess, (state, { paginadoDecano }): DecanoState => ({
        ...state,
        paginacionDecano: paginadoDecano,
        loading: false,
        loaded: true,
    })),
    on(fromActions.paginacionDecanoFail, (state, { mensaje }): DecanoState => ({
        ...state,
        loading: false,
        error: mensaje,
    })),
    on(fromActions.editarDecano, (state, { decano }): DecanoState => ({
        ...state,
        decano: decano,
        loadingGuardado: true,
        loadedGuardado: false,
        error: null,
    })),
    on(fromActions.editarDecanoSuccess, (state, { decano }): DecanoState => ({
        ...state,
        decano: decano,
        loadingGuardado: false,
        loadedGuardado: true,
    })),
    on(fromActions.editarDecanoFail, (state, { error }): DecanoState => ({
        ...state,
        loadingGuardado: false,
        error: error,
    })),
    on(fromActions.resetDecano, (): DecanoState => initState),
);

export function decanoReducer(state: DecanoState | undefined, action: Action) {
    return _decanoReducer(state, action);
}