import { Action, createReducer, on } from '@ngrx/store';


import * as fromActions from '../../actions/aerc-actions/resolucion-aerc.actions';

import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { ResolucionAercModel } from '../../../commons/models/aerc-models/resolucion-aerc.model';

export interface ResolucionesAercState {
  loading: boolean;
  error: any;
  resolucion: ResolucionAercModel;
  listadoResoluciones: Array<ResolucionAercModel>;
  listadoResolucionesPaginated: IPaginacion<ResolucionAercModel>;
  searchPagination: SearchPagination<string>;
  tipoSolicitud: number;
  estadoSolicitud: number;
  periodo: number;
}

const initState: ResolucionesAercState = {
  loading: false,
  error: null,
  resolucion: null,
  listadoResoluciones: null,
  listadoResolucionesPaginated: null,
  searchPagination: null,
  tipoSolicitud: null,
  estadoSolicitud: null,
  periodo: null,
};

const _resolucionAercReducer = createReducer(initState,

  on(fromActions.listarResoluciones, (state, { searchPagination, estadoSolicitud, tipoSolicitud, periodo }): ResolucionesAercState => ({
    ...state,
    loading: true,
    error: null,
    tipoSolicitud: tipoSolicitud,
    estadoSolicitud: estadoSolicitud,
    periodo: periodo,
    searchPagination: searchPagination,
  })),
  on(fromActions.listarResolucionesSuccess, (state, { listadoResolucionesPaginated }): ResolucionesAercState => ({
    ...state,
    loading: false,
    listadoResolucionesPaginated: listadoResolucionesPaginated,
  })),
  on(fromActions.listarResolucionesFail, (state, { mensaje }): ResolucionesAercState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),
  on(fromActions.unsetSolicitud, (): ResolucionesAercState => initState),
);

export function resolucionAercReducer(state: ResolucionesAercState | undefined, action: Action) {
  return _resolucionAercReducer(state, action);
}

