import { Action, createReducer, on } from '@ngrx/store';
import { Paso } from 'src/app/commons/enums/paso-enum';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { DatoPagoModel } from 'src/app/commons/models/dato-pago.model';
import { IndicadorPublicacionesModel } from 'src/app/commons/models/gestion-academica/indicador-publicaciones.model';
import { ResultadoPagoModel } from 'src/app/commons/models/resultado-pago.model';

import * as fromActions from '../actions/indicador-publicaciones.actions'

export interface IndicadorPublicacionesState {
    loading: boolean;
    loaded: boolean;
    saving: boolean;
    saved: boolean;
    success: boolean;
    listaIndicadorPublicaciones: IndicadorPublicacionesModel[];
    error: any;
    paginacionIndicadorPublicaciones: IPaginacion<IndicadorPublicacionesModel>;
    searchPagination: SearchPagination<String>;
    indicadorPublicaciones: IndicadorPublicacionesModel;
}

const initState: IndicadorPublicacionesState = {
    loading: false,
    loaded: false,
    saving: false,
    saved: false,
    listaIndicadorPublicaciones: null,
    success: null,
    error: null,
    paginacionIndicadorPublicaciones: null,
    searchPagination: null,
    indicadorPublicaciones: null,

};

const _indicadorPublicacionesReducer = createReducer(initState,
    on(
        fromActions.paginacionIndicadorPublicaciones,
        (state, { searchPagination }): IndicadorPublicacionesState => ({
            ...state,
            loading: true,
            loaded: false,
            error: null,
            paginacionIndicadorPublicaciones: null,
            searchPagination: searchPagination,
        })
    ),
    on(
        fromActions.paginacionIndicadorPublicacionesSuccess,
        (state, { paginadoPeriodo }): IndicadorPublicacionesState => ({
            ...state,
            paginacionIndicadorPublicaciones: paginadoPeriodo,
            loading: false,
            loaded: true,

        })
    ),
    on(
        fromActions.paginacionIndicadorPublicacionesFail,
        (state, { error }): IndicadorPublicacionesState => ({
            ...state,
            loading: false,
            error: error,
        })
    ),
    on(fromActions.guardarIndicadorPublicaciones, (state, { indicadorPublicaciones }): IndicadorPublicacionesState => ({
        ...state,
        indicadorPublicaciones: indicadorPublicaciones,
        loading: true,
        loaded: false,
        error: null,
    })),
    on(fromActions.guardarIndicadorPublicacionesSuccess, (state, { indicadorPublicaciones }): IndicadorPublicacionesState => ({
        ...state,
        indicadorPublicaciones: indicadorPublicaciones,
        loading: false,
        loaded: true,
    })),
    on(fromActions.guardarIndicadorPublicacionesFail, (state, { error }): IndicadorPublicacionesState => ({
        ...state,
        loading: false,
        error: error,
    })),

    on(fromActions.resetIndicadorPublicaciones, (): IndicadorPublicacionesState => initState),
);

export function indicadorPublicacionesReducer(state: IndicadorPublicacionesState | undefined, action: Action) {
    return _indicadorPublicacionesReducer(state, action);
}

