import { Injectable } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as resolucionActions from '../actions/resolucion.actions';

import { ResolucionService } from 'src/app/commons/services/resolucion.service';
import { concatMap, map, catchError } from 'rxjs/operators';
import { of } from 'rxjs';
@Injectable()
export class ResolucionEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private resolucionService: ResolucionService,
    
    ) { }
    paginacionResolucion$ = createEffect(
        () => this.actions$.pipe(
            ofType(resolucionActions.paginacionResolucion),
            concatMap(
                (data) => this.resolucionService.pagination(data.searchPagination).pipe(
                    map(resul => {

                        if (resul != null) {
                            return resolucionActions.paginacionResolucionSuccess({ paginadoResolucion: resul });
                        }

                        const msgError = `Ocurrió un problema inesperado al intentar obtener las Resoluciones`;
                        this.toastr.error(msgError);
                        return resolucionActions.paginacionResolucionFail({ mensaje: msgError });

                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Resoluciones`;
                            this.toastr.error(msgError);
                            return of(resolucionActions.paginacionResolucionFail({ mensaje: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(resolucionActions.paginacionResolucionFail({ mensaje: error?.error?.message }));
                    })
                )
            )
        )
    );
}