import { Injectable } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as compromisoActions from '../actions/compromiso.actions';

import { concatMap, map, catchError, exhaustMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { CompromisoService } from 'src/app/commons/services/compromiso.service';
@Injectable()
export class CompromisoEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private compromisoService: CompromisoService,

    ) { }
    paginacionResolucion$ = createEffect(
        () => this.actions$.pipe(
            ofType(compromisoActions.paginationCompromiso),
            concatMap(
                (data) => this.compromisoService.pagination(data.searchPagination, data.anio, data.estado).pipe(
                    map(resul => {

                        if (resul != null) {
                            return compromisoActions.paginationCompromisoSuccess({ paginadoCompromiso: resul });
                        }

                        const msgError = `Ocurrió un problema inesperado al intentar obtener las Resoluciones`;
                        this.toastr.error(msgError);
                        return compromisoActions.paginationCompromisoFail({ error: msgError });

                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Resoluciones`;
                            this.toastr.error(msgError);
                            return of(compromisoActions.paginationCompromisoFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(compromisoActions.paginationCompromisoFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );

    guardarCompromiso$ = createEffect(() =>
        this.actions$.pipe(
            ofType(compromisoActions.guardarCompromiso),
            exhaustMap(action =>
                this.compromisoService.save(action.compromiso).pipe(
                    map(resul => {
                        if (resul != null) {
                            return compromisoActions.guardarCompromisoSuccess({ compromiso: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar guardar el compromiso`;
                        this.toastr.error(msgError);
                        return compromisoActions.guardarCompromisoFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar guardar el compromiso`;
                            this.toastr.error(msgError);
                            return of(compromisoActions.guardarCompromisoFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(compromisoActions.guardarCompromisoFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );

    guardarCompromisoMasTarde$ = createEffect(() =>
        this.actions$.pipe(
            ofType(compromisoActions.guardarCompromisoMasTarde),
            exhaustMap(action =>
                this.compromisoService.saveMasTarde(action.compromiso).pipe(
                    map(resul => {
                        if (resul != null) {
                            return compromisoActions.guardarCompromisoMasTardeSuccess({ compromiso: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar guardar el compromiso`;
                        this.toastr.error(msgError);
                        return compromisoActions.guardarCompromisoMasTardeFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar guardar el compromiso`;
                            this.toastr.error(msgError);
                            return of(compromisoActions.guardarCompromisoMasTardeFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(compromisoActions.guardarCompromisoMasTardeFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}