import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { ResolucionModel } from '../models/gestion-academica/resolucion.model';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';

@Injectable({
    providedIn: 'root'
})
export class ResolucionService {

    constructor(
        private http: HttpClient,
    ) { }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<ResolucionModel>> {
        const url = `${environment.backend.gestion_academica}api/resolucion/v1/pagination`;
        return this.http.post<IPaginacion<ResolucionModel>>(url, searchPagination);
    }

    public getAll(): Observable<string[]> {
        return this.http.get<string[]>(`${environment.backend.gestion_academica}api/resolucion/v1/get-all`);
    }

}