import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { ResolucionAercModel } from '../../models/aerc-models/resolucion-aerc.model';

@Injectable({
  providedIn: 'root'
})
export class ResolucionesService {


  private urlBase = `${environment.backend.aerc}api/resoluciones`;
  constructor(
    private http: HttpClient,
  ) { }

  public listarResoluciones(searchPagination: SearchPagination<string>,
    estadoSolicitud: number, tipoSolicitud: number, periodoAcademico: number): Observable<IPaginacion<ResolucionAercModel>> {
    return this.http.post<IPaginacion<ResolucionAercModel>>(`${this.urlBase}/get-all-resoluciones/paginated/${estadoSolicitud}/${tipoSolicitud}/${periodoAcademico}`, searchPagination);
  }

  public getResolucionByIdSolicitud(idSolicitud: number): Observable<ResolucionAercModel> {
    return this.http.get<ResolucionAercModel>(`${this.urlBase}/find-by-id/${idSolicitud}`);
  }

}
