import { Action, createReducer, on } from '@ngrx/store';
import { PreguntaRespuestaModel } from 'src/app/commons/models/aerc-models/pregunta-respuesta.model';

import * as fromActions from '../../actions/aerc-actions/formulario.actions';

export interface FormularioState {
  loading: boolean;
  error: any;
  tipoSolicitud: number;
  listadoSolicitud: PreguntaRespuestaModel[];
  listadoEncuesta: PreguntaRespuestaModel[];
  idSolicitud: number;
}

const initState: FormularioState = {
  loading: false,
  error: null,
  tipoSolicitud: null,
  listadoSolicitud: null,
  listadoEncuesta: null,
  idSolicitud: null,
};

const _formularioReducer = createReducer(initState,

  on(fromActions.listarPreguntaSolicitud, (state, { tipoSolicitud, idSolicitud }): FormularioState => ({
    ...state,
    loading: true,
    error: null,
    tipoSolicitud: tipoSolicitud,
    idSolicitud: idSolicitud
  })),
  on(fromActions.listarPreguntaSolicitudSuccess, (state, { listadoSolicitud }): FormularioState => ({
    ...state,
    loading: false,
    listadoSolicitud: listadoSolicitud
  })),
  on(fromActions.listarPreguntaSolicitudFail, (state, { mensaje }): FormularioState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.getEncuesta, (state, { idSolicitud }): FormularioState => ({
    ...state,
    loading: true,
    error: null,
    idSolicitud: idSolicitud
  })),
  on(fromActions.getEncuestaSuccess, (state, { listadoEncuesta }): FormularioState => ({
    ...state,
    loading: false,
    listadoEncuesta: listadoEncuesta
  })),
  on(fromActions.getEncuestaFail, (state, { mensaje }): FormularioState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.unsetFormulario, (): FormularioState => initState),
);

export function formularioReducer(state: FormularioState | undefined, action: Action) {
  return _formularioReducer(state, action);
}

