import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ComentariosDialogData } from '../../interfaces/comentarios-dialog-data.interface';

@Component({
  selector: 'app-comentarios-dialog',
  templateUrl: './comentarios-dialog.component.html',
  styleUrls: ['./comentarios-dialog.component.css'],
})
export class ComentariosDialogComponent implements OnInit {

  comentarios: string = "";

  constructor(
    public dialogRef: MatDialogRef<ComentariosDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ComentariosDialogData) { }

  ngOnInit(): void {
  }

  onNoClick() {
    this.dialogRef.close();
  }
}
