import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable, throwError } from 'rxjs';
import { descargarEncuesta, descargarEncuestaFail, descargarEncuestaSuccess } from 'src/app/store/actions/compromiso.actions';
import { AppState } from 'src/app/store/app.reducer';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { catchError, map, tap } from 'rxjs/operators';
import { SearchPagination } from '../interfaces/search.pagination';
import { saveAs } from 'file-saver';


import { CertificadoModel } from '../models/certificado.model';
import { CompromisoModel } from '../models/gestion-academica/compromiso.model';
import { ProgramaModel } from '../models/programa.model';
import Swal from 'sweetalert2';
import { UsuarioSignModel } from '../models/externo/usuario-sign.model';


@Injectable({
    providedIn: 'root'
})
export class CompromisoService {

    constructor(
        private http: HttpClient,
        private store: Store<AppState>,
    ) { }

    public pagination(
        searchPagination: SearchPagination<string>,
        anio: string,
        estado: string
    ): Observable<IPaginacion<CompromisoModel>> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/pagination/${estado}/${anio}`;
        return this.http.post<IPaginacion<CompromisoModel>>(url, searchPagination);
    }

    public save(compromiso: CompromisoModel): Observable<CompromisoModel> {
        const url = `${environment.backend.gestion_academica}api/compromiso`;
        return this.http.post<CompromisoModel>(url, compromiso)
    }

    public saveEncargado(compromiso: CompromisoModel): Observable<CompromisoModel> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/save-encargado`;
        return this.http.post<CompromisoModel>(url, compromiso)
    }

    public saveMasTarde(compromiso: CompromisoModel): Observable<CompromisoModel> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/save-after`;
        return this.http.post<CompromisoModel>(url, compromiso)
    }

    public getCompromisoByRut(rut: string): Observable<CompromisoModel[]> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/compromiso-by-docente/${rut}`;
        return this.http.get<CompromisoModel[]>(url)
    }

    public aprobarCompromiso(compromiso: CompromisoModel): Observable<CompromisoModel> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/aprobar-compromiso`;
        return this.http.post<CompromisoModel>(url, compromiso)
    }

    public getEncargadoByUsuarioAD(usuarioAD: string): Observable<any> {
        return this.http.get<any>(
            `${environment.backend.gestion_academica}api/compromiso/v1/get-nombre-by-usuarioad/${usuarioAD}`)
    }
    public getEncargadoByUsuarioEmail(usuarioEmail: string): Observable<any> {
        return this.http.get<any>(
            `${environment.backend.gestion_academica}api/compromiso/v1/get-usuario-by-email/${usuarioEmail}`)
    }
    public saveEncargadoSign(usuario: UsuarioSignModel): Observable<any> {
        return this.http.post<any>(`${environment.backend.gestion_academica}api/compromiso/v1/save-sign`, usuario);
    }
    public saveRoleSign(usuario: UsuarioSignModel, tipo: string): Observable<any> {
        return this.http.post<any>(`${environment.backend.gestion_academica}api/compromiso/v1/save-role-sign/${tipo}`, usuario);
    }

    public descargarEncuesta(codigoCurso: string, rut: string, periodo: string, nrc: string): void {
        this.store.dispatch(descargarEncuesta());
        const headers = new HttpHeaders();
        headers.append('Accept', 'application/pdf');
        this.http.get(`${environment.backend.gestion_academica}api/docente/v1/get-encuesta-docente-pdf/${rut}/${codigoCurso}/${periodo}/${nrc}`, {
            headers: headers,
            responseType: 'blob'
        }).pipe(
            tap((resul: any) => {
                if (resul.error) {
                    this.store.dispatch(descargarEncuestaFail());
                    Swal.fire({
                        title: 'Error',
                        html: "<p>Se ha producido un error al intentar obtener el reporte</p>",
                        icon: 'error',
                        allowOutsideClick: false,
                        allowEscapeKey: false
                    });
                }

            }), catchError(e => {
                this.store.dispatch(descargarEncuestaFail());
                if (e.status !== 401 || e.status !== 403) {
                    Swal.fire({
                        title: 'Error',
                        html: 'Hubo un problema al procesar la solicitud',
                        icon: 'error',
                        allowOutsideClick: false,
                        allowEscapeKey: false
                    });
                    return throwError(new Error(e.error));
                }
            })).subscribe(res => {
                this.store.dispatch(descargarEncuestaSuccess());
                saveAs(<any>res, `encuesta${rut}.pdf`);
            });
    }

    public getCompromisoByAnio(anio: string): Observable<CompromisoModel[]> {
        const url = `${environment.backend.gestion_academica}api/compromiso/v1/get-compromisos-by-anio/${anio}`;
        return this.http.get<CompromisoModel[]>(url)
    }


}