import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { DecanoModel } from '../models/gestion-academica/decano.model';

@Injectable({
    providedIn: 'root'
})
export class DecanoService {

    constructor(
        private http: HttpClient,
    ) { }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<DecanoModel>> {
        const url = `${environment.backend.gestion_academica}api/decano/v1/pagination`;
        return this.http.post<IPaginacion<DecanoModel>>(url, searchPagination);
    }

    public save(decano: DecanoModel): Observable<DecanoModel> {
        return this.http.post<DecanoModel>(`${environment.backend.gestion_academica}api/decano/v1`, decano);
    }


}