import { Action, createReducer, on } from '@ngrx/store';

import { Paso } from 'src/app/commons/enums/paso-enum';

import * as fromActions from '../../actions/aerc-actions/solicitud.actions';

import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';

export interface SolicitudState {
  loading: boolean;
  error: any;
  solicitud: SolicitudModel;
  listadoSolicitudes: Array<SolicitudModel>;
  listadoSolicitudesPaginacion: IPaginacion<SolicitudModel>;
  searchPagination: SearchPagination<string>;
  tipoSolicitud: number;
  estadoSolicitud: number;
  periodo: number;
  isAprobador: boolean;
}

const initState: SolicitudState = {
  loading: false,
  error: null,
  solicitud: null,
  listadoSolicitudes: null,
  listadoSolicitudesPaginacion: null,
  searchPagination: null,
  tipoSolicitud: null,
  estadoSolicitud: null,
  periodo: null,
  isAprobador: null,
};

const _solicitudReducer = createReducer(initState,

  on(fromActions.listarSolicitudes, (state, { searchPagination, estadoSolicitud, tipoSolicitud, periodo }): SolicitudState => ({
    ...state,
    loading: true,
    error: null,
    tipoSolicitud: tipoSolicitud,
    estadoSolicitud: estadoSolicitud,
    periodo: periodo,
    searchPagination: searchPagination,
  })),
  on(fromActions.listarSolicitudesSuccess, (state, { listadoSolicitudesPaginacion }): SolicitudState => ({
    ...state,
    loading: false,
    listadoSolicitudesPaginacion: listadoSolicitudesPaginacion,
  })),
  on(fromActions.listarSolicitudesFail, (state, { mensaje }): SolicitudState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.listarSolicitudesInnovacion, (state, { searchPagination, estadoSolicitud, tipoSolicitud, isAprobador }): SolicitudState => ({
    ...state,
    loading: true,
    error: null,
    tipoSolicitud: tipoSolicitud,
    estadoSolicitud: estadoSolicitud,
    searchPagination: searchPagination,
    isAprobador: isAprobador
  })),
  on(fromActions.listarSolicitudesInnovacionSuccess, (state, { listadoSolicitudesPaginacion }): SolicitudState => ({
    ...state,
    loading: false,
    listadoSolicitudesPaginacion: listadoSolicitudesPaginacion,
  })),
  on(fromActions.listarSolicitudesInnovacionFail, (state, { mensaje }): SolicitudState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.getSolicitudesByTipo, (state, { tipoSolicitud }): SolicitudState => ({
    ...state,
    loading: true,
    error: null,
    tipoSolicitud: tipoSolicitud,
  })),
  on(fromActions.getSolicitudesByTipoSuccess, (state, { listadoSolicitudes }): SolicitudState => ({
    ...state,
    loading: false,
    listadoSolicitudes: listadoSolicitudes,
  })),
  on(fromActions.getSolicitudesByTipoFail, (state, { mensaje }): SolicitudState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.setVistaSolicitud, (state, { solicitud }): SolicitudState => ({
    ...state,
    loading: true,
    error: null,
    solicitud: solicitud,
  })),

  on(fromActions.unsetSolicitud, (): SolicitudState => initState),
);

export function solicitudReducer(state: SolicitudState | undefined, action: Action) {
  return _solicitudReducer(state, action);
}

