import { Injectable } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as decanoActions from '../actions/decano.actions';
import { concatMap, map, catchError, exhaustMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { DecanoService } from 'src/app/commons/services/decano.service';
@Injectable()
export class DecanoEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private decanoService: DecanoService,

    ) { }
    paginacionDecano$ = createEffect(
        () => this.actions$.pipe(
            ofType(decanoActions.paginacionDecano),
            concatMap(
                (data) => this.decanoService.pagination(data.searchPagination).pipe(
                    map(resul => {

                        if (resul != null) {
                            return decanoActions.paginacionDecanoSuccess({ paginadoDecano: resul });
                        }

                        const msgError = `Ocurrió un problema inesperado al intentar obtener los Decanos`;
                        this.toastr.error(msgError);
                        return decanoActions.paginacionDecanoFail({ mensaje: msgError });

                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Decanos`;
                            this.toastr.error(msgError);
                            return of(decanoActions.paginacionDecanoFail({ mensaje: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(decanoActions.paginacionDecanoFail({ mensaje: error?.error?.message }));
                    })
                )
            )
        )
    );

    editarDecano$ = createEffect(() =>
        this.actions$.pipe(
            ofType(decanoActions.editarDecano),
            exhaustMap(action =>
                this.decanoService.save(action.decano).pipe(
                    map(resul => {
                        if (resul != null) {
                            return decanoActions.editarDecanoSuccess({ decano: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar editar el correo del decano`;
                        this.toastr.error(msgError);
                        return decanoActions.editarDecanoFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar editar el correo del decano`;
                            this.toastr.error(msgError);
                            return of(decanoActions.editarDecanoFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(decanoActions.editarDecanoFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}