import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, concatMap, exhaustMap, map } from 'rxjs/operators';
import { PeriodoService } from 'src/app/commons/services/periodo.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as periodoActions from '../actions/periodo.actions';

@Injectable()
export class PeriodoEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private periodoService: PeriodoService,
        private alert: UtilAlert,
        private router: Router
    ) { }

    getPeriodos$ = createEffect(() =>
        this.actions$.pipe(
            ofType(periodoActions.getPeriodos),
            exhaustMap(_action =>
                this.periodoService.getAllPeriodosCompleto().pipe(
                    map(resul => {
                        if (resul != null && resul.length > 0) {
                            return periodoActions.getPeriodosSuccess({ listaPeriodos: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar obtener los periodos`;
                        this.toastr.error(msgError);
                        return periodoActions.getPeriodosFail({ mensaje: msgError });
                    }),
                    catchError(error => {
                        this.toastr.error(error?.error?.message);
                        return of(periodoActions.getPeriodosFail({ mensaje: error?.error?.message }));
                    })
                )
            )
        )
    );

    paginationPeriodo$ = createEffect(() =>
        this.actions$.pipe(
            ofType(periodoActions.paginationPeriodo),
            concatMap((data) =>
                this.periodoService
                    .pagination(data.searchPagination)
                    .pipe(
                        map((resul) => {
                            if (resul != null) {
                                return periodoActions.paginationPeriodoSuccess({
                                    paginadoPeriodo: resul,
                                });
                            }
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                            this.toastr.error(msgError);
                            return periodoActions.paginationPeriodoFail({
                                error: msgError,
                            });
                        }),
                        catchError((error) => {
                            if (error.status == 0) {
                                const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                                this.toastr.error(msgError);
                                return of(
                                    periodoActions.paginationPeriodoFail({
                                        error: msgError,
                                    })
                                );
                            }
                            this.toastr.error(error?.error?.message);
                            return of(
                                periodoActions.paginationPeriodoFail({
                                    error: error?.error?.message,
                                })
                            );
                        })
                    )
            )
        )
    );

    guardarPeriodo$ = createEffect(() =>
        this.actions$.pipe(
            ofType(periodoActions.guardarPeriodo),
            exhaustMap(action =>
                this.periodoService.save(action.periodo).pipe(
                    map(resul => {
                        if (resul != null) {
                            return periodoActions.guardarPeriodoSuccess({ periodo: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar guardar el periodo`;
                        this.toastr.error(msgError);
                        return periodoActions.guardarPeriodoFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar guardar el periodo`;
                            this.toastr.error(msgError);
                            return of(periodoActions.guardarPeriodoFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(periodoActions.guardarPeriodoFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}