import { createAction, props } from '@ngrx/store';
import { IPaginacion } from 'src/app/commons/interfaces/paginacion.interface';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { ResolucionAercModel } from '../../../commons/models/aerc-models/resolucion-aerc.model';

export const listarResoluciones = createAction(
    '[RESOLUCIONES MODEL] Listar resoluciones',
    props<{ searchPagination: SearchPagination<string>, estadoSolicitud: number, tipoSolicitud: number, periodo: number }>()
);
export const listarResolucionesSuccess = createAction(
    '[RESOLUCIONES MODEL] Listar resoluciones success',
    props<{ listadoResolucionesPaginated: IPaginacion<ResolucionAercModel> }>()
);
export const listarResolucionesFail = createAction(
    '[RESOLUCIONES MODEL] Listar resoluciones fail',
    props<{ mensaje: string }>()
);

export const unsetSolicitud = createAction(
    '[RESOLUCIONES MODEL] Unset resoluciones state');

