import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, concatMap, exhaustMap, map } from 'rxjs/operators';
import { ConfiguracionCompromisoService, } from 'src/app/commons/services/configuracion-compromiso.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as configuracionCompromisosActions from '../actions/configuracion-compromiso.actions';

@Injectable()
export class ConfiguracionCompromisosEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private configuracionCompromisoService: ConfiguracionCompromisoService,
        private alert: UtilAlert,
        private router: Router
    ) { }
    paginacionConfiguracionCompromiso$ = createEffect(() =>
        this.actions$.pipe(
            ofType(configuracionCompromisosActions.paginacionConfiguracionCompromisos),
            concatMap((data) =>
                this.configuracionCompromisoService
                    .pagination(data.searchPagination)
                    .pipe(
                        map((resul) => {
                            if (resul != null) {
                                return configuracionCompromisosActions.paginacionConfiguracionCompromisosSuccess({
                                    paginadoPeriodo: resul,
                                });
                            }
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                            this.toastr.error(msgError);
                            return configuracionCompromisosActions.paginacionConfiguracionCompromisosFail({
                                error: msgError,
                            });
                        }),
                        catchError((error) => {
                            if (error.status == 0) {
                                const msgError = `Ocurrió un problema inesperado al obtener el listado de Periodos`;
                                this.toastr.error(msgError);
                                return of(
                                    configuracionCompromisosActions.paginacionConfiguracionCompromisosFail({
                                        error: msgError,
                                    })
                                );
                            }
                            this.toastr.error(error?.error?.message);
                            return of(
                                configuracionCompromisosActions.paginacionConfiguracionCompromisosFail({
                                    error: error?.error?.message,
                                })
                            );
                        })
                    )
            )
        )
    );

    guardarConfiguracionCompromiso$ = createEffect(() =>
        this.actions$.pipe(
            ofType(configuracionCompromisosActions.guardarConfiguracionCompromisos),
            exhaustMap(action =>
                this.configuracionCompromisoService.save(action.configuracionCompromiso).pipe(
                    map(resul => {
                        if (resul != null) {
                            return configuracionCompromisosActions.guardarConfiguracionCompromisosSuccess({ configuracionCompromiso: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar guardar la configuracion de compromisos`;
                        this.toastr.error(msgError);
                        return configuracionCompromisosActions.guardarConfiguracionCompromisosFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar guardar la configuracion de compromisos`;
                            this.toastr.error(msgError);
                            return of(configuracionCompromisosActions.guardarConfiguracionCompromisosFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(configuracionCompromisosActions.guardarConfiguracionCompromisosFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}