import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, exhaustMap, map } from 'rxjs/operators';
import { FormularioService } from 'src/app/commons/services/aerc-services/formulario.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as formularioActions from '../../actions/aerc-actions/formulario.actions';

@Injectable()
export class FormularioEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toastr: ToastrService,
    private actions$: Actions,
    private alert: UtilAlert,
    private formularioService: FormularioService,
  ) { }

  listarPreguntaSolicitud$ = createEffect(() =>
    this.actions$.pipe(
      ofType(formularioActions.listarPreguntaSolicitud),
      exhaustMap(action =>
        this.formularioService.getPreguntasByTipoSolicitud(action.tipoSolicitud, action.idSolicitud).pipe(
          map(result => {
            if (result != null) {
              return formularioActions.listarPreguntaSolicitudSuccess({ listadoSolicitud: result });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las preguntas de la solicitud`;
            this.toastr.error(msgError);
            return formularioActions.listarPreguntaSolicitudFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(formularioActions.listarPreguntaSolicitudFail({ mensaje: error?.error?.message }));
          })
        )
      )
    )
  );

  getEncuesta$ = createEffect(() =>
    this.actions$.pipe(
      ofType(formularioActions.getEncuesta),
      exhaustMap(action =>
        this.formularioService.getPreguntasByTipoSolicitud(4, action.idSolicitud).pipe(
          map(result => {
            if (result != null) {
              return formularioActions.getEncuestaSuccess({ listadoEncuesta: result });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las preguntas de la encuesta`;
            this.toastr.error(msgError);
            return formularioActions.getEncuestaFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(formularioActions.getEncuestaFail({ mensaje: error?.error?.message }));
          })
        )
      )
    )
  );

}
