import * as resolucionDocenteActions from '../actions/resolucion-docente.actions';
import { Injectable } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { ResolucionService } from '../../commons/services/resolucion.service';
import { exhaustMap, map, catchError } from 'rxjs/operators';
import { of } from 'rxjs';
import { ResolucionDocenteService } from '../../commons/services/resoluciondocente.service';
@Injectable()
export class ResolucionDocenteEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private resolucionDocenteService: ResolucionDocenteService,
    
    ) { }

     guardarResolucionDocente$ = createEffect(() =>
    this.actions$.pipe(
      ofType(resolucionDocenteActions.guardarResolucionDocente),
      exhaustMap(action =>
        this.resolucionDocenteService.save(action.resolucionDocente).pipe(
          map(resul => {
            if (resul != null) {
              return resolucionDocenteActions.guardarResolucionDocenteSuccess({ resolucionDocente: resul });
            }
            const msgError = `Ocurrió un problema inesperado al intentar guardar la resolucionDocente`;
            this.toastr.error(msgError);
            return resolucionDocenteActions.guardarResolucionDocenteFail({ error: msgError });
          }),
          catchError(error => {
            if (error.status == 0) {
              const msgError = `Ocurrió un problema inesperado al intentar guardar la resolucionDocente`;
              this.toastr.error(msgError);
              return of(resolucionDocenteActions.guardarResolucionDocenteFail({ error: msgError }));
            }
            this.toastr.error(error?.error?.message);
            return of(resolucionDocenteActions.guardarResolucionDocenteFail({ error: error?.error?.message }));
          })
        )
      )
    )
  );
}