import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IndicadorPublicacionesModel } from '../models/gestion-academica/indicador-publicaciones.model';
import { SearchPagination } from '../interfaces/search.pagination';
import { IPaginacion } from '../interfaces/paginacion.interface';
@Injectable({
    providedIn: 'root'
})
export class IndicadorPublicacionesService {

    constructor(
        private http: HttpClient,
    ) { }

    public findByFacultadAndTipoAcademico(facultadId: string, tipoAcademico: string): Observable<IndicadorPublicacionesModel> {
        return this.http.get<IndicadorPublicacionesModel>(`${environment.backend.gestion_academica}api/indicador-publicaciones/v1/find-by-facultad-tipoacademico/${facultadId}/${tipoAcademico}`);
    }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<IndicadorPublicacionesModel>> {
        const url = `${environment.backend.gestion_academica}api/indicador-publicaciones/v1/pagination`;
        return this.http.post<IPaginacion<IndicadorPublicacionesModel>>(url, searchPagination);
    }

    public save(indicadorPublicacion: IndicadorPublicacionesModel): Observable<IndicadorPublicacionesModel> {
        return this.http.post<IndicadorPublicacionesModel>(`${environment.backend.gestion_academica}api/indicador-publicaciones`, indicadorPublicacion);
    }

}