import { Action, createReducer, on } from '@ngrx/store';
import { EstadoSolicitudModel } from 'src/app/commons/models/aerc-models/estado-solicitud-model';


import * as fromActions from '../../actions/aerc-actions/estado-solicitud.actions';

export interface EstadoSolicitudState {
  loading: boolean;
  error: any;
  estados: Array<EstadoSolicitudModel>;
  tipoSolicitud: string;
}

const initState: EstadoSolicitudState = {
  loading: false,
  error: null,
  estados: null,
  tipoSolicitud: '',
};

const _estadoSolicitudReducer = createReducer(initState,

  on(fromActions.getEstadoSolicitud, (state, { tipoSolicitud }): EstadoSolicitudState => ({
    ...state,
    loading: true,
    error: null,
    estados: null,
    tipoSolicitud: tipoSolicitud,
  })),
  on(fromActions.getEstadoSolicitudSuccess, (state, { estados }): EstadoSolicitudState => ({
    ...state,
    loading: false,
    estados: estados
  })),
  on(fromActions.getEstadoSolicitudFail, (state, { mensaje }): EstadoSolicitudState => ({
    ...state,
    loading: false,
    error: mensaje,
  })),

  on(fromActions.unsetEstadoSolicitud, (): EstadoSolicitudState => initState),
);

export function estadoSolicitudReducer(state: EstadoSolicitudState | undefined, action: Action) {
  return _estadoSolicitudReducer(state, action);
}

