import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { LoginService } from 'src/app/paginas/oauth/login/login.service';

import * as appActions from '../actions/auth.actions';

@Injectable()
export class AuthEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toasrtService: ToastrService,
    private actions$: Actions,
    private loginService: LoginService,
    private authenticationService: AuthenticationService,
  ) { }


  @Effect()
  authenticar$ = this.actions$.pipe(
    ofType(appActions.autenticar),
    switchMap(data => {
      const login: { usuario: string; password: string; tipo: string } = data['login'];
      return this.loginService.login(login.usuario, login.password, login.tipo).pipe(
        map(resul => {
          this.authenticationService.guardarStorage(resul);
          return appActions.autenticarSuccess({ respuesta: resul });
        }),
        catchError(error => {
          if (error?.error?.error_description == 'Bad credentials') {
            // Bad credentials
            this.toasrtService.error('Usuario o clave incorrectas', '', { disableTimeOut: false, timeOut: 10000, closeButton: true });
          } else if (error?.error?.error_description == 'Usuario o claves inválidos') {
            // User is disabled
            this.toasrtService.error('Usuario o clave incorrectas.', 'Error de Credenciales', { disableTimeOut: true, closeButton: true });
          } else if (error?.error?.error_description == 'User is disabled') {
            // User is disabled
            this.toasrtService.error('Le hemos enviado un correo para que pueda habilitar su cuenta.', 'Cuenta inactiva', { disableTimeOut: true, closeButton: true });
          } else if (error?.error?.error_description == 'User account has expired') {
            // User account has expired
            this.toasrtService.error('Le hemos enviado un correo para activar su cuanta.', 'Su cuenta ha expirado', { disableTimeOut: true, closeButton: true });
          } else if (error?.error?.error_description == 'User credentials have expired') {
            // User credentials have expired
            this.toasrtService.error('Le hemos enviado un correo para que pueda habilitar su cuenta.', 'Su clave ha expirado', { disableTimeOut: true, closeButton: true });
          } else if (error?.error?.error_description == 'User account is locked') {
            // User account is locked
            this.toasrtService.error('Le hemos enviado un correo para que pueda desbloquear su cuenta.', 'Cuenta bloqueada', { disableTimeOut: true, closeButton: true });
          } else {
            // Other
            this.toasrtService.error('No se pudo autenticar, intentelo más tarde.', '', { disableTimeOut: true, closeButton: true, });
          }
          return of(appActions.autenticarFail({ error: error }));
        })
      );
    })
  );

}
