import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { of } from 'rxjs';
import { catchError, exhaustMap, map } from 'rxjs/operators';
import { UtilAlert } from 'src/app/commons/util/util.alert';

import * as resolucionActions from '../../actions/aerc-actions/resolucion-aerc.actions';
import { Router } from '@angular/router';
import { ResolucionesService } from '../../../commons/services/aerc-services/resoluciones.service';

@Injectable()
export class ResolucionesAercEffects {

  @BlockUI() blockUIPage: NgBlockUI;
  constructor(
    private toastr: ToastrService,
    private actions$: Actions,
    private alert: UtilAlert,
    private resolucionesService: ResolucionesService,
    private router: Router
  ) { }

  listarSolicitudes$ = createEffect(() =>
    this.actions$.pipe(
      ofType(resolucionActions.listarResoluciones),
      exhaustMap(action =>
        this.resolucionesService.listarResoluciones(action.searchPagination, action.estadoSolicitud, action.tipoSolicitud, action.periodo).pipe(
          map(salida => {
            if (salida != null) {
              return resolucionActions.listarResolucionesSuccess({ listadoResolucionesPaginated: salida });
            }
            const msgError = `Ocurrió un problema inesperado al intentar obtener las resoluciones creadas`;
            return resolucionActions.listarResolucionesFail({ mensaje: msgError });
          }),
          catchError(error => {
            this.toastr.error(error?.error?.message);
            return of(resolucionActions.listarResolucionesFail({ mensaje: error?.error?.message }));
          })
        )
      )
    ))

}
