import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { DecanoModel } from '../models/gestion-academica/decano.model';
import { DirectorModel } from '../models/gestion-academica/director.model';
import { UsuarioSignModel } from '../models/externo/usuario-sign.model';

@Injectable({
    providedIn: 'root'
})
export class DirectorService {

    constructor(
        private http: HttpClient,
    ) { }

    public pagination(searchPagination: SearchPagination<string>): Observable<IPaginacion<DirectorModel>> {
        const url = `${environment.backend.gestion_academica}api/director/v1/pagination`;
        return this.http.post<IPaginacion<DirectorModel>>(url, searchPagination);
    }

    public save(director: DirectorModel): Observable<DirectorModel> {
        return this.http.post<DirectorModel>(`${environment.backend.gestion_academica}api/director/v1`, director);
    }

    public saveRoleSign(usuario: UsuarioSignModel): Observable<any> {
        return this.http.post<any>(`${environment.backend.gestion_academica}api/director/v1/save-role-sign`, usuario);
    }


}