import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { PreguntaRespuestaModel } from '../../models/aerc-models/pregunta-respuesta.model';
import { FormularioPreguntaModel } from '../../models/aerc-models/formulario-pregunta.model';
import { FormularioRespuestaModel } from '../../models/aerc-models/formulario-respuesta.model';


@Injectable({
  providedIn: 'root'
})
export class FormularioService {

  constructor(
    private http: HttpClient,
  ) { }

  public getPreguntasByTipoSolicitud(tipoSolicitud: number, idSolicitud: number): Observable<PreguntaRespuestaModel[]> {
    return this.http.get<PreguntaRespuestaModel[]>(`${environment.backend.aerc}api/formulario/v1/find-preguntas/${tipoSolicitud}?idSolicitud=${idSolicitud}`);
  }

  public getAllPreguntas(tipoSolicitud: number): Observable<FormularioPreguntaModel[]> {
    return this.http.get<FormularioPreguntaModel[]>(`${environment.backend.aerc}api/formulario/v1/find-all-preguntas/${tipoSolicitud}`);
  }

  public getAllRespuestas(pregunta: number): Observable<FormularioRespuestaModel[]> {
    return this.http.get<FormularioRespuestaModel[]>(`${environment.backend.aerc}api/formulario/v1/find-all-respuestas/${pregunta}`);
  }

  public saveAllPreguntas(preguntas: FormularioPreguntaModel[]): Observable<boolean> {
    return this.http.post<boolean>(`${environment.backend.aerc}api/formulario/v1/save-all/preguntas`, preguntas);
  }
  public saveAllRespuestas(respuestas: FormularioRespuestaModel[]): Observable<boolean> {
    return this.http.post<boolean>(`${environment.backend.aerc}api/formulario/v1/save-all/respuestas`, respuestas);
  }

}
