import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { PeriodoAcademicoModel } from '../../models/aerc-models/periodo-academico-model';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';
import { AprobadorAercModel } from '../../models/aerc-models/aprobador-aerc.model';
import { ResultadoProc } from '../../interfaces/resultado-proc.interface';
import { AprobadorSignAercModel } from '../../models/aerc-models/aprobador-sign.model';

@Injectable({
  providedIn: 'root'
})
export class AprobadorAercService {


  private urlBase = `${environment.backend.aerc}api/aprobador`;
  constructor(
    private http: HttpClient,
  ) { }

  public listarAprobadores(searchPagination: SearchPagination<string>,
    escuela: string, facultad: string, tipo: number): Observable<IPaginacion<AprobadorAercModel>> {
    return this.http.post<IPaginacion<AprobadorAercModel>>(`${this.urlBase}/v1/paginated/${escuela}/${facultad}/${tipo}`, searchPagination);
  }

  public buscarAprobadorSign(usuario: string): Observable<ResultadoProc<AprobadorSignAercModel>> {
    return this.http.get<ResultadoProc<AprobadorSignAercModel>>(`${this.urlBase}/v1/find-ad?usuario=${usuario}`);
  }

  public editAprobador(aprobador: AprobadorAercModel) {
    return this.http.post<AprobadorAercModel>(`${this.urlBase}/v1/save`, aprobador);
  }

  public findAprobadorByAuth(): Observable<Array<AprobadorAercModel>> {
    return this.http.get<Array<AprobadorAercModel>>(`${this.urlBase}/v1/find/username`);
  }

  public saveNewAprobador(aprobador: AprobadorAercModel) {
    return this.http.post<AprobadorAercModel>(`${this.urlBase}/v1/save-sign`, aprobador);
  }

  public findAprobadorInnovacion(): Observable<AprobadorAercModel[]> {
    return this.http.get<AprobadorAercModel[]>(`${this.urlBase}/v1/find-cur`);
  }

  public findUsuarioSolicitud(): Observable<AprobadorAercModel> {
    return this.http.get<AprobadorAercModel>(`${this.urlBase}/v1/find-aca`);
  }


}
