import * as fromActions from '../actions/compromiso.actions';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';
import { ResolucionModel } from 'src/app/commons/models/gestion-academica/resolucion.model';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { Action, createReducer, on } from '@ngrx/store';
import { CompromisoModel } from 'src/app/commons/models/gestion-academica/compromiso.model';

export interface CompromisoState {
    loading: boolean;
    loadingGuardado: boolean;
    loaded: boolean;
    loadedGuardado: boolean;
    paginationCompromiso: IPaginacion<CompromisoModel>;
    compromiso: CompromisoModel;
    error: any;
    searchPagination: SearchPagination<String>;
    mensaje: string;
    masTarde: boolean;
    downloadingEncuesta: boolean;
}

const initState: CompromisoState = {
    loading: false,
    loadingGuardado: false,
    loadedGuardado: false,
    loaded: false,
    paginationCompromiso: null,
    error: null,
    searchPagination: null,
    mensaje: null,
    compromiso: null,
    masTarde: false,
    downloadingEncuesta: false,


};

const _compromisoReducer = createReducer(initState,

    on(fromActions.descargarEncuesta, (state): CompromisoState => ({
        ...state,
        downloadingEncuesta: true,
    })),
    on(fromActions.descargarEncuestaSuccess, (state): CompromisoState => ({
        ...state,
        downloadingEncuesta: false,
    })),
    on(fromActions.descargarEncuestaFail, (state): CompromisoState => ({
        ...state,
        downloadingEncuesta: false,
    })),

    on(fromActions.paginationCompromiso, (state, { searchPagination }): CompromisoState => ({
        ...state,
        loading: true,
        loaded: false,
        error: null,
        paginationCompromiso: null,
        compromiso: null,
        searchPagination: searchPagination
    })),
    on(fromActions.paginationCompromisoSuccess, (state, { paginadoCompromiso }): CompromisoState => ({
        ...state,
        paginationCompromiso: paginadoCompromiso,
        loading: false,
        loaded: true,
    })),
    on(fromActions.paginationCompromisoFail, (state, { error }): CompromisoState => ({
        ...state,
        loading: false,
        error: error,
    })),
    on(fromActions.setCompromisoEdit, (state, { compromiso }): CompromisoState => ({
        ...state,
        loaded: false,
        compromiso: compromiso,
    })),
    on(fromActions.guardarCompromiso, (state, { compromiso }): CompromisoState => ({
        ...state,
        compromiso: compromiso,
        loadingGuardado: true,
        loadedGuardado: false,
        error: null,
    })),
    on(fromActions.guardarCompromisoSuccess, (state, { compromiso }): CompromisoState => ({
        ...state,
        compromiso: compromiso,
        loadingGuardado: false,
        loadedGuardado: true,
        masTarde: false,
    })),
    on(fromActions.guardarCompromisoFail, (state, { error }): CompromisoState => ({
        ...state,
        loadingGuardado: false,
        error: error,
    })),
    on(fromActions.guardarCompromisoMasTarde, (state, { compromiso }): CompromisoState => ({
        ...state,
        compromiso: compromiso,
        loadingGuardado: true,
        loadedGuardado: false,
        error: null,
        masTarde: true,
    })),
    on(fromActions.guardarCompromisoMasTardeSuccess, (state, { compromiso }): CompromisoState => ({
        ...state,
        compromiso: compromiso,
        loadingGuardado: false,
        loadedGuardado: true,
    })),
    on(fromActions.guardarCompromisoMasTardeFail, (state, { error }): CompromisoState => ({
        ...state,
        loadingGuardado: false,
        error: error,
    })),
    on(fromActions.resetCompromisos, (): CompromisoState => initState),
);

export function compromisoReducer(state: CompromisoState | undefined, action: Action) {
    return _compromisoReducer(state, action);
}