import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

import { CertificadoModel } from '../models/certificado.model';
import { DatoPagoModel } from '../models/dato-pago.model';

@Injectable({
  providedIn: 'root'
})
export class PagoService {

  constructor(
    private http: HttpClient,
  ) { }

  public pagarCuotas(rutPasaporte: string, cuotas: Array<number>, medioPago: "webpay" | "khipu"): Observable<DatoPagoModel> {
    return this.http.post<DatoPagoModel>(`${environment.backend.gestion_academica}api/pago/v1/cuota/${medioPago}/pagar/${rutPasaporte}`, cuotas);
  }

  public pagarCertificados(rutPasaporte: string, certificados: Array<CertificadoModel>, medioPago: "webpay" | "khipu"): Observable<DatoPagoModel> {
    return this.http.post<DatoPagoModel>(`${environment.backend.gestion_academica}api/pago/v1/certificado/${medioPago}/pagar/${rutPasaporte}`, certificados);
  }

  public procesarPagoCuotaKhipu(paymentId: string): Observable<any> {
    return this.http.post<DatoPagoModel>(`${environment.backend.gestion_academica}resultado/khipu/pago-cuota/procesar?paymentId=${paymentId}`, {});
  }

}
