import { createAction, props } from "@ngrx/store";
import { SearchPagination } from "../../commons/interfaces/search.pagination";
import { IPaginacion } from "../../commons/interfaces/paginacion.interface";
import { CompromisoModel } from 'src/app/commons/models/gestion-academica/compromiso.model';


//PAGINATION SERVICIO

export const descargarEncuesta = createAction('[ENCUESTA MODEL] Descargar encuesta');
export const descargarEncuestaSuccess = createAction('[ENCUESTA MODEL] Descargar encuesta success');
export const descargarEncuestaFail = createAction('[ENCUESTA MODEL] Descargar encuesta fail');

export const paginationCompromiso = createAction(
    "[COMPROMISO PAGINATION] Listar all compromisos",
    props<{ searchPagination: SearchPagination<string>, anio: string, estado: string }>()
);
export const paginationCompromisoSuccess = createAction(
    "[COMPROMISO PAGINATION] Listar all compromisos success",
    props<{ paginadoCompromiso: IPaginacion<CompromisoModel> }>()
);
export const paginationCompromisoFail = createAction(
    "[COMPROMISO PAGINATION] Listar all compromisos fail",
    props<{ error: any }>()
);

export const setCompromisoEdit = createAction(
    '[COMPROMISO] EDITAR COMPROMISO',
    props<{ compromiso: CompromisoModel }>()
);

export const guardarCompromiso = createAction(
    '[COMPROMISO] Guardar compromiso',
    props<{ compromiso: CompromisoModel }>()
);
export const guardarCompromisoSuccess = createAction(
    '[COMPROMISO] Guardar compromiso success',
    props<{ compromiso: CompromisoModel }>()
);
export const guardarCompromisoFail = createAction(
    '[COMPROMISO] Guardar compromiso fail',
    props<{ error: any }>()
);
export const guardarCompromisoMasTarde = createAction(
    '[COMPROMISO] Guardar compromiso mas tarde',
    props<{ compromiso: CompromisoModel }>()
);
export const guardarCompromisoMasTardeSuccess = createAction(
    '[COMPROMISO] Guardar compromiso mas tarde success',
    props<{ compromiso: CompromisoModel }>()
);
export const guardarCompromisoMasTardeFail = createAction(
    '[COMPROMISO] Guardar compromiso mas tarde fail',
    props<{ error: any }>()
);
export const resetCompromisos = createAction(
    '[COMPROMISO] RESET COMPROMISOS'
);
