import { createAction, props } from '@ngrx/store';
import { SearchPagination } from '../../commons/interfaces/search.pagination';
import { IPaginacion } from '../../commons/interfaces/paginacion.interface';//////////ººº¬¬¬¬===ººººººº¬¬¬¬¬===
import { DecanoModel } from 'src/app/commons/models/gestion-academica/decano.model';


// PAGINACION
export const paginacionDecano = createAction(
    '[DECANO MODEL] Paginación Decano',
    props<{ searchPagination: SearchPagination<string> }>()
);
export const paginacionDecanoSuccess = createAction(
    '[DECANO MODEL] Paginación Decano Success',
    props<{ paginadoDecano: IPaginacion<DecanoModel> }>()
);
export const paginacionDecanoFail = createAction(
    '[DECANO MODEL] Paginación Decano Fail',
    props<{ mensaje: string }>()
);

export const editarDecano = createAction(
    '[DECANO MODEL] Editar Decano',
    props<{ decano: DecanoModel }>()
);
export const editarDecanoSuccess = createAction(
    '[DECANO MODEL] Editar Decano success',
    props<{ decano: DecanoModel }>()
);
export const editarDecanoFail = createAction(
    '[DECANO MODEL] Editar Decano fail',
    props<{ error: any }>()
);
export const resetDecano = createAction(
    '[DECANO MODEL] RESET DECANOS'
);