import { Injectable } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import * as directorActions from '../actions/director.actions';
import { concatMap, map, catchError, exhaustMap } from 'rxjs/operators';
import { of } from 'rxjs';
import { DirectorService } from 'src/app/commons/services/director.service';
@Injectable()
export class DirectorEffects {

    @BlockUI() blockUIPage: NgBlockUI;
    constructor(
        private toastr: ToastrService,
        private actions$: Actions,
        private directorService: DirectorService,

    ) { }
    paginacionDirector$ = createEffect(
        () => this.actions$.pipe(
            ofType(directorActions.paginacionDirector),
            concatMap(
                (data) => this.directorService.pagination(data.searchPagination).pipe(
                    map(resul => {

                        if (resul != null) {
                            return directorActions.paginacionDirectorSuccess({ paginadoDirector: resul });
                        }

                        const msgError = `Ocurrió un problema inesperado al intentar obtener los Director`;
                        this.toastr.error(msgError);
                        return directorActions.paginacionDirectorFail({ mensaje: msgError });

                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al obtener el listado de Director`;
                            this.toastr.error(msgError);
                            return of(directorActions.paginacionDirectorFail({ mensaje: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(directorActions.paginacionDirectorFail({ mensaje: error?.error?.message }));
                    })
                )
            )
        )
    );

    editarDirector$ = createEffect(() =>
        this.actions$.pipe(
            ofType(directorActions.editarDirector),
            exhaustMap(action =>
                this.directorService.save(action.Director).pipe(
                    map(resul => {
                        if (resul != null) {
                            return directorActions.editarDirectorSuccess({ Director: resul });
                        }
                        const msgError = `Ocurrió un problema inesperado al intentar editar el correo del director`;
                        this.toastr.error(msgError);
                        return directorActions.editarDirectorFail({ error: msgError });
                    }),
                    catchError(error => {
                        if (error.status == 0) {
                            const msgError = `Ocurrió un problema inesperado al intentar editar el correo del director`;
                            this.toastr.error(msgError);
                            return of(directorActions.editarDirectorFail({ error: msgError }));
                        }
                        this.toastr.error(error?.error?.message);
                        return of(directorActions.editarDirectorFail({ error: error?.error?.message }));
                    })
                )
            )
        )
    );
}