import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { ResolucionModel } from '../models/gestion-academica/resolucion.model';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { DocenteModel } from '../models/gestion-academica/docente.model';
import { ResolucionDocenteModel } from '../models/gestion-academica/resolucion-docente.model';

@Injectable({
    providedIn: 'root'
})
export class ResolucionDocenteService {

    constructor(
        private http: HttpClient,
    ) { }

    public getDocenteByNumeroResolucion(numeroResolucion: string): Observable<Array<DocenteModel>> {
        return this.http.get<Array<DocenteModel>>(`${environment.backend.gestion_academica}api/resoluciondocente/v1/get-docente-by-numero-resolucion/${numeroResolucion}`);
    }

    public save(ResolucionesDocentes: Array<ResolucionDocenteModel>): Observable<Array<ResolucionDocenteModel>> {
        return this.http.post<Array<ResolucionDocenteModel>>(`${environment.backend.gestion_academica}api/resoluciondocente`, ResolucionesDocentes);
    }

}