import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { EstadoSolicitudModel } from '../../models/aerc-models/estado-solicitud-model';

@Injectable({
  providedIn: 'root'
})
export class EstadoSolicitudService {

  private urlBase = `${environment.backend.aerc}api/estados`;

  constructor(
    private http: HttpClient,
  ) { }

  public getAllEstadoSolicitud(tipoSolicitud: string): Observable<Array<EstadoSolicitudModel>> {
    return this.http.get<Array<EstadoSolicitudModel>>(`${this.urlBase}/v1/find/aprobador/${tipoSolicitud}`);
  }


}
