import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

import { CertificadoModel } from '../models/certificado.model';
import { ProgramaModel } from '../models/programa.model';

@Injectable({
  providedIn: 'root'
})
export class CertificadoService {

  constructor(
    private http: HttpClient,
  ) { }

  public getAllCertificados(): Observable<Array<CertificadoModel>> {
    return this.http.get<Array<CertificadoModel>>(`${environment.backend.gestion_academica}api/certificado/v1/get-all`);
  }

  public sendCertificado(programa: ProgramaModel): Observable<Boolean> {
    return this.http.post<Boolean>(`${environment.backend.gestion_academica}api/certificado/v1/temp-send`, programa);
  }

}
