import { createAction, props } from '@ngrx/store';
import { ResolucionDocenteModel } from 'src/app/commons/models/gestion-academica/resolucion-docente.model';

export const guardarResolucionDocente = createAction(
    '[RESOLUCIONDOCENTE MODEL] Guardar resolucionDocente',
    props<{ resolucionDocente: ResolucionDocenteModel[] }>()
);
export const guardarResolucionDocenteSuccess = createAction(
    '[RESOLUCIONDOCENTE MODEL] Guardar resolucionDocente success',
    props<{ resolucionDocente: ResolucionDocenteModel[] }>()
);
export const guardarResolucionDocenteFail = createAction(
    '[RESOLUCIONDOCENTE MODEL] Guardar resolucionDocente fail',
    props<{ error: any }>()
);
export const destroyResolucionDocente = createAction('[RESOLUCIONDOCENTE MODEL] Reset State');